.searchFormContainer {
	border: 5px solid #eeeeee;
	padding: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.searchInputContainer {
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-negative: 1;
	    flex-shrink: 1;
}

.searchIconContainer {
	-ms-flex-positive: 0;
	    flex-grow: 0;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	margin-left: 10px;
	cursor: pointer;
}

.searchInput {
	border: none;
	font-size: 18px;
	width: 100%;
	font-family: 'Montserrat', sans-serif;
}

.searchInput:focus {
	outline: none;
}

.searchFormTitle {
	text-align: center;
	margin-bottom: 30px;
	font-size: 30px;
	font-family: 'Montserrat', sans-serif;
}


