.productDetailContainer {
	display: -ms-flexbox;
	display: flex;
}

.productContentContainer {
	background-color: #f9f9f9;
}

@media screen and (min-width: 1100px) {
	.productDetailContainer {
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-align: stretch;
		    align-items: stretch;
		height: 100vh;
	}

	.productContentContainer {
		-ms-flex: 0 0 330px;
		    flex: 0 0 330px;
		overflow-y: auto;
		padding: 60px 50px;
	}

	.productImageContainer {
		-ms-flex: 1 1 0%;
		    flex: 1 1 0%;
	}

	.productImageContainer {
		overflow: auto;
	}

	.productImageContainer::-webkit-scrollbar-track, 
	.productContentContainer::-webkit-scrollbar-track {
		border-radius: 10px;
		background-color: #d5d5d5;
	}

	.productImageContainer::-webkit-scrollbar,
	.productContentContainer::-webkit-scrollbar {
		width: 8px;
		background-color: #d5d5d5;
	}

	.productImageContainer::-webkit-scrollbar-thumb,
	.productContentContainer::-webkit-scrollbar-thumb {
		border-radius: 10px;
		background-color: #e0cc3c;
	}
}

@media screen and (max-width: 1099px) {
	.productDetailContainer {
		-ms-flex-direction: column-reverse;
		    flex-direction: column-reverse;
	}

	.productImageContainer {
	}

	.productContentContainer {
		width: 100%;
		padding: 50px 50px;
	}
}

.copyrightLittle {
	background-color: #1d1d1d;
	text-align: center;
}

@media screen and (min-width: 900px) {
	.copyrightLittle {
		display: none;
	}
}

@media screen and (max-width: 899px) {
	.copyrightLittle {
		display: block;
	}
}



